@use '../foundations' as found;

@include found.mq(pc){
  .l-nav{
    margin: 0 20px 0 0;

    &__list{
      @include found.flexbox;
      justify-content: flex-end;

      &__item{
        a{
          display: block;
          font-size: 1.6rem;

          &:hover{
            color: found.$base_color;
          }
        }

        &:not(:last-of-type){
          margin: 0 20px 0 0;
        }
      }
    }
  }
}

@include found.mq(sp) {
  .l-nav{
    &.is-open{
      .l-nav{
        &__list{
          z-index: 1;
    width: 100%;
    height: 100vh;
    background: found.$base_color;
    position: absolute;
    top: 0;
    left: 0;
        }
      }
    }
    
  }
}
