@use "function" as function;
@use "mixin" as mixin;

*, *:before, *:after {
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: 'Noto Sans JP', sans-serif, "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  color: #000;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  transition: .3s;
  
  &:hover {
    text-decoration: none;
  }
}
img {
  max-width: 100%;
  vertical-align: middle;
  object-fit: cover;
  width: 100%;
}
input {
  margin: 0;
  padding: 0;
}
textarea {
  padding: 0;
  font: inherit;
  color: inherit;  
}
button {
  font: inherit;  
  padding: 0;
  background-color: transparent;
  border: none;
  color: inherit;  
  cursor: pointer;
}

h2{
  font-size: 3rem;
}
h3{
  font-size: 2.4rem;
}

@include mixin.mq(pc){
  .sp{
    display: none;
  }
}
@include mixin.mq(sp){
  .pc{
    display: none;
  }
}